.dlabnav {
  width: 20.5rem;
  padding-bottom: 0;
  height: 100%;
  position: absolute;
  top: 7.5rem;
  padding-top: 0;
  z-index: 6;
  background-color: #FFFFFF;
  background-color: var(--sidebar-bg);
  transition: all .2s ease;
  box-shadow: 0rem 0.9375rem 1.875rem 0rem rgba(0, 0, 0, 0.02);
}

.dlabnav .dlabnav-scroll {
  position: relative;
  height: 100%;
}

.dlabnav .header-info2 {
  margin-top: 0.9rem;
  padding: 0 20px;
}

.dlabnav .header-info2 img {
  height: 3.75rem;
  width: 3.75rem;
  border-radius: 1.25rem;
  margin-right: 1.2rem;
}

.dlabnav .header-info2 span {
  font-size: 1rem;
  font-weight: 500;
  color: #393939;
}

.dlabnav ul {
  padding: 0;
  margin: 0;
  list-style: none; 
}

.dlabnav .metismenu {
  display: flex;
  flex-direction: column;
  padding-top: 0.9375rem;
}

.dlabnav .metismenu .fixed {
  position: fixed;
  top: 0;
  width: 100%;
  left: 0;
}

.dlabnav .metismenu > li {
  display: flex;
  flex-direction: column;
}

.dlabnav .metismenu > li a > i {
  font-size: 1.3rem;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: 0;
  height: auto;
  width: auto;
  text-align: center;
  margin-right: 1.25rem;
  border-radius: 0.125rem;
  line-height: 1;
  border-radius: 0.125rem;
}

.dlabnav .metismenu li {
  position: relative;
}

.dlabnav .metismenu ul {
  transition: all .2s ease-in-out;
  position: relative;
  z-index: 1;
  padding: 0.5rem 0;
}

.dlabnav .metismenu a {
  position: relative;
  display: block;
  padding: 0.625rem 1.875rem;
  outline-width: 0;
  color: #759791;
  text-decoration: none;
}

.dlabnav .metismenu ul a {
  padding-top: .5rem;
  padding-bottom: .5rem;
  position: relative;
  font-size: 0.9375rem;
  padding-left: 5rem;
  transition: all 0.5s;
}

.dlabnav .metismenu ul a:hover, .dlabnav .metismenu ul a:focus, .dlabnav .metismenu ul a .active {
  text-decoration: none;
  color: #47ae3b;
  color: var(--primary); 
}

.dlabnav .metismenu ul a:before {
  position: absolute;
  content: none;
  height: 0.125rem;
  width: 0.5rem;
  top: 50%;
  left: 1.75rem;
  border-radius: 3.75rem;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%); 
}

.dlabnav .metismenu .has-arrow:after {
  border-top: 0.3125rem solid #C8C8C8;
  border-left: 0.3125rem solid #C8C8C8;
  border-bottom: 0.3125rem solid transparent;
  border-right: 0.3125rem solid transparent;
  right: 1.875rem;
  top: 48%;
  -webkit-transform: rotate(-225deg) translateY(-50%);
  transform: rotate(-225deg) translateY(-50%);
}


.metismenu .has-arrow:after {
  position: absolute;
  content: "";
  width: .5em;
  height: .5em;
  border-width: 1px 0 0 1px;
  border-style: solid;
  border-color: initial;
  right: 1em;
  -webkit-transform: rotate(-45deg) translateY(-50%);
  transform: rotate(140deg) translateY(-50%);
  -webkit-transform-origin: top;
  transform-origin: top;
  top: 50%;
  transition: all .3s ease-out;
}

.dlabnav .metismenu li .active {
  color: #47ae3b;
  color: var(--primary); 
  background-color: #cef1ca; 
  background-color: var(--rgba-primary-1);
}

.dlabnav .metismenu > li > .active:before {
  position:absolute;
  background: #47ae3b;
  background: var(--primary);
  content: "";
  height: 100%;
  top: 0;
  right: 0rem;
  transition: all 0.5s;
  border-radius: 1.25rem;
}

@media only screen and (max-width: 87.5rem) {

  .dlabnav {
    width: 15rem;
    top: 4.5rem;
    height: 100%;
  } 
  .dlabnav .metismenu > li > .active:before {
     width: 0.600rem;
  }
}

@media only screen and (max-width: 74.9375rem) {
