nav:after {
	content: "";
	display: table;
	clear: both;
}

nav ul {
	float: right;
	padding: 0;
	margin: 0;
	list-style: none;
	position: relative;
}

nav ul li {
	margin: 0px;
	display: inline-block;
	float: left;
	background-color: #0e5462;
}

nav a {
	display: block;
	padding: 20px 25px;
