	font-size: 17px;
	text-decoration: none;
	font-size: 12px;
}

nav a:hover {
	color: #6ea8bf;
}

nav ul ul {
	display: none;
	position: absolute;

	top: 60px;
}

nav ul li:hover>ul {
	display: inherit;
	overflow: visible;
}

nav ul ul li {
	width: 170px;
	float: none;
	display: list-item;
	position: relative;
}

nav ul ul ul li {
	position: relative;
	top: -60px;
	left: 170px;
}

li>a:after {
	content: "";
}

li>a:only-child:after {
	content: "";
}

/* Media Queries
  --------------------------------------------- */

@media all and (max-width: 768px) {
	#logo {
		display: block;
		padding: 0;
		width: 100%;
		text-align: center;
		float: none;
	}

	nav {
		margin: 0;
	}

	.toggle+a,
	.menu {
		display: none;
	}

	.toggle {
		display: block;
		background-color: #0e5462;
		padding: 14px 20px;
		color: #fff;
		font-size: 12px;
		text-decoration: none;
		border: none;
	}

	.toggle:hover {
		background-color: #0e5462;
		color: #6ea8bf;
	}

	[id^="drop"]:checked+ul {
		display: block;
	}

	nav ul li {
		display: block;
		width: 100%;
	}


	nav ul ul .toggle,
	nav ul ul a {
		padding: 0 10px;
	}

	nav ul ul ul a {
		padding: 0 80px;
	}

	nav ul li ul li .toggle,
	nav ul ul a,
	nav ul ul ul a {
		padding: 14px 10px;
		color: #fff;
		font-size: 12px;
	}

	nav ul li ul li .toggle,
	nav ul ul a {
		font-size: 10px;
	}

	nav ul ul {
		float: none;
		position: static;
		color: #6ea8bf;
		font-size: 12px;
	}

	nav ul ul li:hover>ul,
	nav ul li:hover>ul {
		display: none;
		color: #254441;
		font-size: 12px;
	}

	nav ul ul li {
		display: block;
		width: 100%;
		font-size: 12px;
	}

	nav ul ul ul li {
		position: static;
	}
}

@media all and (max-width: 330px) {
	nav ul li {
		display: block;
		width: 94%;
	}
}
