h1 {
  font-size: 10px;
  text-align: center;
  color: #fff;
}

h2 {
  line-height: 34px;

}

h3 {
  font-size: 10px;
  line-height: 34px;
  text-align: center;
  color: #fff;
}

h3 a {
  color: #fff;
}

a {
  color: #fff;
}

h1 {
  margin-top: 100px;
  text-align: center;
  font-size: 20px;
  line-height: 70px;

}

#container {
  margin: 0 auto;
  max-width: 890px;
}

p {
  text-align: center;
}

.toggle,
[id^="drop"] {
  display: none;
  font-size: 10px;
}

nav {
  margin: 0;
  padding: 0;
  background-color: linear-gradient(90deg, hsl(190, 75%, 22%) 0%, hsl(191, 70%, 22%) 100%);
}

#logo {
  display: block;
  padding: 0 55px;
  float: left;
  font-size: 18px;
  line-height: 60px;
  color: white;
}

nav:after {
  content: "";
  display: table;
  clear: both;
}

nav ul {
  float: right;
  padding: 0;
  margin: 0;
  list-style: none;
  position: relative;
}

nav ul li {
  margin: 0px;
  display: inline-block;
  float: left;
  background-color: #0e5462;
}

nav a {
  display: block;
  padding: 20px 25px;
  color: #fff;
  font-size: 17px;
  text-decoration: none;
  font-size: 12px;
}

nav a:hover {
  color: #6ea8bf;
}

nav ul ul {
  display: none;
  position: absolute;

  top: 60px;
}

nav ul li:hover>ul {
  display: inherit;
  overflow: visible;
}

nav ul ul li {
  width: 170px;
  float: none;
  display: list-item;
  position: relative;
}

nav ul ul ul li {
  position: relative;
  top: -60px;
  left: 170px;
}

li>a:after {
  content: "";
}

li>a:only-child:after {
  content: "";
}

/* Media Queries
  --------------------------------------------- */

@media all and (max-width: 768px) {
  #logo {
    display: block;
    padding: 0;
    width: 100%;
    text-align: center;
    float: none;
  }

  nav {
    margin: 0;
  }

  .toggle+a,
  .menu {
    display: none;
  }

  .toggle {
    display: block;
    background-color: #0e5462;
    padding: 14px 20px;
    color: #fff;
    font-size: 12px;
    text-decoration: none;
    border: none;
  }

  .toggle:hover {
    background-color: #0e5462;
    color: #6ea8bf;
  }

  [id^="drop"]:checked+ul {
    display: block;
  }

  nav ul li {
    display: block;
    width: 100%;
  }


  nav ul ul .toggle,
  nav ul ul a {
    padding: 0 10px;
  }

  nav ul ul ul a {
    padding: 0 80px;
  }

  nav ul li ul li .toggle,
  nav ul ul a,
  nav ul ul ul a {
    padding: 14px 10px;
    color: #fff;
    font-size: 12px;
  }

  nav ul li ul li .toggle,
  nav ul ul a {
    font-size: 10px;
  }

  nav ul ul {
    float: none;
    position: static;
    color: #6ea8bf;
    font-size: 12px;
  }

  nav ul ul li:hover>ul,
  nav ul li:hover>ul {
    display: none;
    color: #254441;
    font-size: 12px;
  }

  nav ul ul li {
    display: block;
    width: 100%;
    font-size: 12px;
  }

  nav ul ul ul li {
    position: static;
  }

}


@media all and (max-width: 330px) {
  nav ul li {
    display: block;
    width: 94%;
  }

}

